import fillRoundRect from './fillRoundRect';
import { uniqlo } from '../../../metalib';

var AppVals_REMIX_PERTICLE_SPEED = 1.0;
var CNV = 20;
export const CIRCLE = 'circle';
export const SQUARE = 'square';
export const ROUND_RECT = 'round_rect';

function V3D13_MosaicImpl(_weakMode) {
  this._LODLevel = [];
  this.updatePointsX = []; // 需要更新的马赛克的x坐标
  this.updatePointsY = []; // 需要更新的马赛克的y坐标
  this.updatePointsLod = [];// 需要更新的马赛克的长度
  this.weakMode = _weakMode;
  this._mozWidth = 40;
  this._mozHeight = 48;
  this._mosicRemderSize = 24;
  this._martigX = 32;
  this._martigY = 0;
  this._lodSum = 1;
  if (this.weakMode) {
    this._mozWidth *= 3 / 2.0;
    this._mozHeight *= 3 / 2.0;
    this._mosicRemderSize *= 2 / 3.0;
  }
}

V3D13_MosaicImpl.prototype.dispose = function () {
  this._LODLevel = [];
  this.updatePointsX = [];
  this.updatePointsY = [];
  this.updatePointsLod = [];
  delete this._LODLevel;
  delete this.updatePointsX;
  delete this.updatePointsY;
  delete this.updatePointsLod;
};

V3D13_MosaicImpl.prototype.reset = function () {
  const { width, height, type, front } = this;
  this._lodSum = 1;
  this.tempCtx.clearRect(0, 0, width, height);
  this.tempCtx.drawImage(front, 0, 0, width, height);
  this._LODLevel = [...new Array(this._mozWidth * this._mozHeight).keys()].map((i) => {
    var x = i % this._mozWidth;
    var y = Math.floor(i / this._mozWidth);
    return [x, y, 1, i];
  });
  this.drawType(type);
};

V3D13_MosaicImpl.prototype.getPixel = function (x, y, d, tmpPixelData) {
  var lod = d;
  var width = this.width;

  var i = Math.ceil(x * this._mosicRemderSize + this._martigX);
  var j = Math.ceil(y * this._mosicRemderSize + this._martigY);
  var totalr = 0;
  var totalg = 0;
  var totalb = 0;
  var totala = 0;
  var sizeX = Math.ceil(this._mosicRemderSize) * lod;
  var sizeY = Math.ceil(this._mosicRemderSize) * lod;
  var totalnum = 0;
  for (var dx = 0; dx < sizeX; dx++) {
    for (var dy = 0; dy < sizeY; dy++) {
      var tmpX = i + dx;
      var tmpY = j + dy;
      var p = (tmpX + tmpY * width) * 4;
      if (tmpPixelData[p] || tmpPixelData[p + 1] || tmpPixelData[p + 2] || tmpPixelData[p + 3]) {
        totalr += tmpPixelData[p];
        totalg += tmpPixelData[p + 1];
        totalb += tmpPixelData[p + 2];
        totala += tmpPixelData[p + 3];
        totalnum++;
      }
    }
  }
  if (!totalnum) {
    return [0,0,0,0];
  }
  var resr = Math.round(totalr / totalnum);
  var resg = Math.round(totalg / totalnum);
  var resb = Math.round(totalb / totalnum);
  var resa = totala / totalnum / 255;
  return [resr, resg, resb, resa];//'rgba(' + resr + ',' + resg + ',' + resb + ',' + resa + ')';
};
/**
 * 取色算法
 * 获取坐标(x, y)起，变成为d的范围内的色值
 * 注：x,y,d均为逻辑坐标，即(0, 0), (0,1)最大为(15, 15)，d最大为5
 *    要经过转化(*MOSIC*SIZE)
 * 去除了透明色的获取
 */
V3D13_MosaicImpl.prototype.getColor = function (x, y, d, tmpPixelData) {
  return `rgba(${this.getPixel(x, y, d, tmpPixelData).join(',')})`
};

/**
 * 绘圆
 */
V3D13_MosaicImpl.prototype.drawCircle = function (x, y, d, data) {
  var color = this.getColor(x, y, d, data);
  this.tempCtx.clearRect(x * this._mosicRemderSize + this._martigX, y * this._mosicRemderSize + this._martigY, d * this._mosicRemderSize, d * this._mosicRemderSize);
  this.tempCtx.beginPath();
  this.tempCtx.fillStyle = color;
  this.tempCtx.arc(x * this._mosicRemderSize + d * this._mosicRemderSize / 2 + this._martigX, y * this._mosicRemderSize + d * this._mosicRemderSize / 2 + this._martigY, d * this._mosicRemderSize / 2 - 2, 0, Math.PI * 2, true);
  this.tempCtx.fill();
  this.tempCtx.closePath();
};

/**
 * 绘方
 */
V3D13_MosaicImpl.prototype.drawSquare = function (x, y, d, data) {
  var color = this.getColor(x, y, d, data);
  this.tempCtx.clearRect(x * this._mosicRemderSize + this._martigX, y * this._mosicRemderSize + this._martigY, d * this._mosicRemderSize, d * this._mosicRemderSize);
  this.tempCtx.beginPath();
  this.tempCtx.fillStyle = color;
  this.tempCtx.fillRect(x * this._mosicRemderSize + this._martigX, y * this._mosicRemderSize + this._martigY, d * this._mosicRemderSize, d * this._mosicRemderSize);
  this.tempCtx.fill();
  this.tempCtx.closePath();
};

/**
 * 绘制圆角矩形（正方形）
 */
V3D13_MosaicImpl.prototype.drawRoundRect = function (x, y, d, data) {
  const sizeX = d * this._mosicRemderSize;
  const sizeY = d * this._mosicRemderSize;
  const left = x * this._mosicRemderSize + this._martigX;
  const top = y * this._mosicRemderSize + this._martigY;
  const ROUND_RECT_RADIUS = Math.floor(sizeX / 8); // 圆角矩形的圆角半径设置
  const color = this.getColor(x, y, d, data);
  this.tempCtx.clearRect(left, top, sizeX, sizeY);
  fillRoundRect(this.tempCtx, left, top, sizeX, sizeY, ROUND_RECT_RADIUS, color);
};

V3D13_MosaicImpl.prototype.clipOffset = function (type) {
  const { width, height, tempCtx } = this;
  if (this._martigX) {
    tempCtx.clearRect(0, 0, this._martigX, height);
    tempCtx.clearRect(width - this._martigX, 0, this._martigX, height);
  } else {
    tempCtx.clearRect(0, 0, width, this._martigY);
    tempCtx.clearRect(0, height - this._martigY, width, this._martigY);
  }
};

/**
 * 遍历马赛克信息，根据类型去渲染
 */
V3D13_MosaicImpl.prototype.drawType = function (type) {
  this.type = type;
  const TYPE_EVENT = {
    [CIRCLE]: this.drawCircle,
    [ROUND_RECT]: this.drawRoundRect,
    [SQUARE]: this.drawSquare,
  };
  const { width, height, tempCtx } = this;
  const imageData = tempCtx.getImageData(0, 0, width, height);
  this.clipOffset();
  this._LODLevel.forEach(v => {
    if (v) {
      var x = v[0];
      var y = v[1];
      var d = v[2];
      TYPE_EVENT[type].call(this, x, y, d, imageData.data);
    }
  });
  this.paint();
};
V3D13_MosaicImpl.prototype.setBlendMode = function (blendMode) {
  this.blendMode = blendMode;
  this.paint();
};

V3D13_MosaicImpl.prototype.paint = function () {
  const { ctx, width, height, back, tempCanvas } = this;
  ctx.clearRect(0, 0, width, height);
  ctx.globalCompositeOperation = 'source-over';
  ctx.globalAlpha = 1;
  if (back) {
    ctx.drawImage(back, 0, 0, width, height);
  }
  if (this.blendMode === uniqlo.BlendMode.ERASE) {
    ctx.globalCompositeOperation = 'destination-in';
  } else if (this.blendMode === uniqlo.BlendMode.MULTIPLY) {
    ctx.globalCompositeOperation = 'multiply';
  } else if (this.blendMode === uniqlo.BlendMode.SCREEN) {
    ctx.globalCompositeOperation = 'lighter';
  } else if (this.blendMode === uniqlo.BlendMode.ALPHA) {
    ctx.globalAlpha = 0.6;
  }
  ctx.drawImage(tempCanvas, 0, 0, width, height);
};
/**
 * 改变马赛克的形状
 * option: circle, square, round_rect
 */
V3D13_MosaicImpl.prototype.changeType = function (type) {
  this.type = type;
  this.drawType(type);
};

/**
 * 初始化canvas，生成默认马赛克宽度的图片
 */
V3D13_MosaicImpl.prototype.init = function (canvas, width, height, back, front, type) {
  this.type = type;
  this.canvas = canvas;
  this.ctx = this.canvas.getContext('2d');
  this.width = width = Math.floor(width);
  this.height = Math.floor(height);
  const tempCanvas = this.tempCanvas = document.createElement('canvas');
  tempCanvas.width = width;
  tempCanvas.height = height;
  this.tempCtx = tempCanvas.getContext('2d');
  if (back) {
    this.back = back;
    this.ctx.drawImage(back, 0, 0, width, height);
  }
  if (front) {
    this.front = front;
    this.ctx.drawImage(front, 0, 0, width, height);
  }
  const widthSize = width / this._mozWidth;
  const heightSize = height / this._mozHeight;
  if (widthSize > heightSize) {
    this._mosicRemderSize = heightSize;
    this._martigX = (width - this._mosicRemderSize * this._mozWidth) / 2;
    this._martigY = 0;
  } else {
    this._mosicRemderSize = widthSize;
    this._martigX = 0;
    this._martigY = (height - this._mosicRemderSize * this._mozHeight) / 2;
  }

  this.generateMipmapWithBitmap(width / this._mozWidth, height / this._mozHeight);
  this.maxCellLod = this._mozHeight * this._mozWidth * (this._mipmaps.length - 1 - 2);
  setTimeout(() => {
    dispatchEvent(new Event('ACTIVATE'));
  }, 10);
};

V3D13_MosaicImpl.prototype.generateMipmapWithBitmap = function (mipWidth, mipHeight) {
  this._mipmaps = [];
  while (mipWidth > 0 && mipHeight > 0) { //产生图像不同级别的纹理贴图，每次规模缩小一半
    // scaleTransform.identity();
    mipWidth >>= 1; //宽度减少一半
    mipHeight >>= 1; //高度减少一般
    this._mipmaps.push(0); //将纹理贴图压栈
  }

}
/**
 * 摇动后根据马赛克信息绘制（只绘制马赛克，因为变化的马赛克数量相对剩余的总是少数）
 * 根据类型选择绘制
 * 绘制完成后把需要绘制的马赛克数组清零
 */
V3D13_MosaicImpl.prototype.draw = function () {
  const TYPE_EVENT = {
    [CIRCLE]: this.drawCircle,
    [ROUND_RECT]: this.drawRoundRect,
    [SQUARE]: this.drawSquare,
  };
  const imageData = this.tempCtx.getImageData(0, 0, this.width, this.height);
  this.updatePointsX.forEach((v, i) => {
    var x = this.updatePointsX[i];
    var y = this.updatePointsY[i];
    var lod = this.updatePointsLod[i];
    TYPE_EVENT[this.type].call(this, x, y, lod, imageData.data);
  });
  this.updatePointsX = [];
  this.updatePointsY = [];
  this.updatePointsLod = [];
  this.paint();
  if (this.onDraw) {
    this.onDraw();
  }
};

V3D13_MosaicImpl.prototype.addedForce = function (speed = 1) {
  var self = this;
  var va = self._lodSum / self.maxCellLod;
  var cnv = (1 - va) * 8
  var x;
  var y;
  var d;
  var td;
  var index;
  var node;
  const { width, height, tempCtx } = self;
  const tmpPixelData = tempCtx.getImageData(0, 0, width, height).data;
  if (cnv < 5) {
    if (Math.random() > (cnv / 5.0 * .5 + 0.05) * AppVals_REMIX_PERTICLE_SPEED)
      return;
  }
  for (let i = 0; i < cnv * AppVals_REMIX_PERTICLE_SPEED * speed; i++) {
    LABEL_RESTART_RANDOM();
  }

  function LABEL_RESTART_RANDOM() {
    index = Math.floor(self._LODLevel.length * Math.random()); // 取随机点算法，此处可优化
    node = self._LODLevel[index];
    if (node) {
      x = node[0];
      y = node[1];
      d = node[2];
      if (d === 8) {
        // 当马赛克半径到8后，就不进行遍历和操作
        return;
      }
      td = d === 8 ? 8 : 2 * d;
      if(self.weakMode && td > 1) {
        td = 2;
      }
      LABEL_RESTART();
    }
  }

  function LABEL_RESTART() {
    if (x % 2 === 1) {
      x = x - 1;
    } else {
      if (Math.floor(x / d) % 2 === 1) {
        x = x - d;
      }
    }
    if (y % 2 === 1) {
      y = y - 1;
    } else {
      if (Math.floor(y / d) % 2 === 1) {
        y = y - d;
      }
    }
    const _LODLevel = self._LODLevel.slice()
    var updatedNode;
    var _miR;
    var _miG;
    var _miB;
    var _maR;
    var _maG;
    var _maB;
    var chk = false;
    var size = 0;
    var size2 = 0;
    for (var ly = y; ly < y + td; ly++) {
      for (var lx = x; lx < x + td; lx++) {
        // 根据一个(x, y, d)，把(x, y)留下来，跨大d，把周围的三大块全部清空
        // eslint-disable-next-line no-loop-func
        self._LODLevel.forEach((v, i) => {
          if (v && v[0] === lx && v[1] === ly) {
            const [__r, __g, __b, __a] = self.getPixel(lx, ly, v[2], tmpPixelData)
            if(__a !== 0) {
              size2++;
            }
            size++;
            if (chk) {
              if (_miR > __r)
                _miR = __r;
              if (_maR < __r)
                _maR = __r;
              if (_miG > __g)
                _miG = __g;
              if (_maG < __g)
                _maG = __g;
              if (_miB > __b)
                _miB = __b;
              if (_maB < __b)
                _maB = __b;
            } else {
              chk = true;
              _miR = _maR = __r;
              _miG = _maG = __g;
              _miB = _maB = __b;
            }
            if (lx === x && ly === y) {
              updatedNode = v;
            } else {
              _LODLevel[i] = undefined;
            }
          }
        });
      }
    }
    const _r = _maR - _miR;
    const _g = _maG - _miG;
    const _b = _maB - _miB;
    var r = Math.sqrt(_r * _r + _g * _g + _b * _b) / ((va) * 500);
    if (self.weakMode) {
      if (r > 0.6 || size !== size2)
        return;
    }
    if(updatedNode) {
      updatedNode[2] = td;
      self._lodSum += size;
    }
    self._LODLevel = _LODLevel.filter(Boolean);
    self.updatePointsX.push(x);
    self.updatePointsY.push(y);
    self.updatePointsLod.push(td);
  }
};

export default V3D13_MosaicImpl;
