function fillRoundRect(cxt, x, y, width, height, radius, fillColor) {
  if (2 * radius > width || 2 * radius > height) {
    return false;
  }

  cxt.save();
  drawRoundRectPath(cxt, x + 1, y + 1, width - 2, height - 2, radius);
  cxt.fillStyle = fillColor || '#000';
  cxt.fill();
  cxt.restore();
}

function drawRoundRectPath(cxt, x, y, width, height, radius) {
  cxt.beginPath();
  cxt.arc(x + radius, y + radius, radius, Math.PI, Math.PI * 3 / 2);
  cxt.lineTo(width - radius + x, y);
  cxt.arc(width - radius + x, radius + y, radius, Math.PI * 3 / 2, Math.PI * 2);
  cxt.lineTo(width + x, height + y - radius);
  cxt.arc(width - radius + x, height - radius + y, radius, 0, Math.PI * 1 / 2);
  cxt.lineTo(radius + x, height + y);
  cxt.arc(radius + x, height - radius + y, radius, Math.PI * 1 / 2, Math.PI);
  cxt.closePath();
}

export default fillRoundRect;
