import React, { useState, useEffect, useContext } from 'react'
import Popup from '../../../components/Popup'
import Locale from '../../../components/Locale'
import { LocaleContext } from '../../../contexts/LocaleContext'
import { CIRCLE, ROUND_RECT, SQUARE } from './V3D13_MosaicImpl'
import invoke from '../../../utils/invoke'

function StyleButton (props) {
  const { onClick, active, border, ...extraProps } = props
  const languageType = useContext(LocaleContext)

  return (
    <div
      onClick={onClick}
      style={{
        display: 'flex',
        justifyContent: 'space-between',
        alignItems: 'center',
        padding: 16,
        borderBottom: border ? 'none' : '1px solid #dadada',
        backgroundColor: '#FFF'
      }}
      {...extraProps}
    >
      <img src={require(`../../../assets/${props.icon}`)} height={24} width={24} />
      <div style={{
        fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
        fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
        fontSize: languageType.current === 'ja' ? '17px' : '20px',
        color: '#1b1b1b',
        textAlign: 'center',
        width: '80%'
      }}>
        <Locale code={props.label} />
      </div>
      {active ? <img src={require(`../../../assets/check.svg`)} height={24} width={24} /> : <div style={{ width: 24, height: 24 }} />}
    </div>
  )
}

export default function StylePicker (props) {
  const { selectedFilterType, setSelectedFilterType } = props
  const languageType = useContext(LocaleContext)
  const [styleIcon, setStyleIcon] = useState('mosaic-square-style')
  const [popupVisible, setPopupVisible] = useState(false)

  function createClickHandler (type, closePopup) {
    return function clickHandler () {
      // invoke('log', 'type' + type)
      // console.log('type' + type)
      if (type === SQUARE) {
        setStyleIcon('mosaic-square-style')
      } else if (type === ROUND_RECT) {
        setStyleIcon('mosaic-round-rect-style')
      } else {
        setStyleIcon('mosaic-circle-style')
      }
      // props.onChange(type)
      setSelectedFilterType(type)
      closePopup()
    }
  }

  function onMouseDownChange () {
    setStyleIcon(styleIcon + '-pressed')
  }

  function onMouseUpChange () {
    let newStyleIcon = styleIcon.replace('-pressed', '')
    setStyleIcon(newStyleIcon)
  }

  return (
    <Popup
      disabled={props.disabled}
      renderOverlay={(overlayProps) => {
        const { triggerRect, closePopup, visible } = overlayProps
        setPopupVisible(visible)
        if (!triggerRect) return <div />
        return (
          <div style={{ position: 'absolute', width: window.innerWidth, bottom: 102, zIndex: 12 }}>
            <div style={{
              color: '#1B1B1B',
              padding: 16,
              borderBottom: '1px solid #dadada',
              fontFamily: languageType.current === 'ja' ? 'NotoSansCJKjp-Bold' : 'initial',
              fontWeight: languageType.current === 'ja' ? 'initial' : 'bold',
              fontSize: languageType.current === 'ja' ? '17px' : '20px',
              backgroundColor: '#FFF',
              textAlign: 'center'
            }}>
              <Locale code='mix.mosaic.style.title' />
            </div>
            <div>
              <StyleButton
                active={selectedFilterType === SQUARE}
                onClick={createClickHandler(SQUARE, closePopup)}
                icon='mosaic-square.svg' label='mix.mosaic.style.angles' />
              <StyleButton
                active={selectedFilterType === ROUND_RECT}
                onClick={createClickHandler(ROUND_RECT, closePopup)}
                icon='mosaic-round-rect.svg' label='mix.mosaic.style.square' />
              <StyleButton
                border='none'
                active={selectedFilterType === CIRCLE}
                onClick={createClickHandler(CIRCLE, closePopup)}
                icon='mosaic-circle.svg' label='mix.mosaic.style.arcs' />
            </div>
            <div style={{
              // marginTop: -2,
              position: 'absolute',
              marginLeft: props.center ? -15 : 26,
              left: props.center ? '50%' : 'initial'
            }}>
              <svg width='30' height='24' viewBox='0 0 30 24'>
                <path
                  fill='#fff'
                  d='M30 0 L15 20 L 0 0 z'
                />
              </svg>
            </div>
          </div>
        )
      }}
    >
      <div
        onTouchStart={() => {
          onMouseDownChange()
        }}
        onTouchEnd={() => {
          onMouseUpChange()
        }}
        style={{
          borderRadius: 50,
          width: 50,
          // marginLeft: 16,
          // marginBottom: 16,
          // textAlign: 'center',
          boxSizing: 'border-box',
          height: 50,
          backgroundColor: 'white',
          paddingLeft: '12px',
          paddingRight: '12px',
          border: '1px solid rgb(221, 221, 221)',
          zIndex: popupVisible ? 13 : 11
        }}
      >
        <img
          src={require(`../../../assets/${styleIcon}.svg`)}
          alt=''
          style={{
            width: '100%',
            height: '100%'
          }}
        />
      </div>
    </Popup>
  )
}
